import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TableListService } from '@app/core/http/table-list.service';
import { YoutubeLink, YoutubeLinkList } from '@app/shared/models/promotion';
import { TableFilter } from '@app/shared/models/table-filter';
import { Subscription } from 'rxjs/internal/Subscription';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})
export class ListComponent implements OnInit {

 

  public tableFilter=<TableFilter>{};
  public isLoading:boolean=false;
  public youtubelinks:YoutubeLink[];
  private subscription:Subscription;
  private isAsc:boolean=false;

  constructor(private _activeRoute:ActivatedRoute,private _tableService:TableListService,) { }

  ngOnInit() {
    this._activeRoute.queryParams.subscribe(
      (params) => {
        if (params.page) {
          this.tableFilter.page = params.page;
          this.tableFilter.limit = params.limit;
        } else if (params.search) {
          this.tableFilter.search = params.search;
        } else{
          this.tableFilter.page = 1;
          this.tableFilter.limit = 10;
          this.tableFilter.sortDirection = 'desc';
          this.tableFilter.sortColumn = 'createdDate';
        }
        this.getYoutubelink();
      }
    )
  }

  getYoutubelink() {
    this.isLoading = true;
    this.subscription = this._tableService.getYoutubeLink(this.tableFilter)
      .subscribe((res:YoutubeLinkList) => {
        this.youtubelinks=res.youtubelinks;
        this.tableFilter = res.filter;
        this.isLoading = false;
      });
  }

  deleteClass(data) {
    this.subscription = this._tableService.onDeleteYoutube(data.linkId)
      .subscribe((res) => {
        this.getYoutubelink();
      })
  }

}
