import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/save.service';
import { YoutubeLink } from '@app/shared/models/promotion';
import { Subscription } from 'rxjs/internal/Subscription';

@Component({
  selector: 'app-promotion-create',
  templateUrl: './promotion-create.component.html',
  styleUrls: ['./promotion-create.component.css']
})
export class PromotionCreateComponent implements OnInit {
  public submitted: boolean = false;
  public sub: Subscription;
  public id: string;

  createForm: FormGroup = this.fb.group({
    title: ["", [Validators.required]],
    link: ["", Validators.required],

  });

  constructor(private fb: FormBuilder, private _createService: SaveService, private _activeRoute: ActivatedRoute) { }





  ngOnInit() {
    this.id = this._activeRoute.snapshot.paramMap.get("id");
    let data = this._activeRoute.snapshot.data['data'];
    if (data)
      this.getDetail(data);

  }

  getDetail(data: YoutubeLink) {
    this.createForm = this.fb.group({
      title: [data.title, [Validators.required]],
      link: [data.link, Validators.required],

    });
  }


  goBack() {
    this._createService.goBackPrev();
  }

  onCreate() {
    console.log(this.createForm.value)
    this.submitted = true;
    if (this.createForm.invalid) {
      return;
    } else {
      this.sub = this._createService.onSaveYoutubeLink(this.createForm.value, this.id).subscribe(
        () => {
          this.goBack();
        });
    }
  }

  get f() {
    return this.createForm.controls;
  }

}
