import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { DetailService } from '@app/core/http/detail.service';
import { PredictionDetail } from '@app/shared/models/prediction';
import { LiveClass } from '@app/shared/models/live-class';
import { YoutubeLink } from '@app/shared/models/promotion';

@Injectable()
export class YoutubeDetailResolver implements Resolve<YoutubeLink> {

    constructor(private question: DetailService) {
    }

    resolve(route: ActivatedRouteSnapshot):
        Observable<YoutubeLink> {

        return this.question.getDetailYoutubeLink(route.params['id']);
    }
}