import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ListComponent } from '@app/modules/manage/promotion/list/list.component';
import { PromotionCreateComponent } from '@app/modules/manage/promotion/promotion-create/promotion-create.component';
import { YoutubeDetailResolver } from '@app/modules/manage/promotion/promotion-detail.relover';

const routes: Routes = [
  {
    path: "",
    component: ListComponent,
  },
  {
    path: "create",
    component: PromotionCreateComponent,
  },
  {
    path: "create/:id",
    component: PromotionCreateComponent,
    resolve: {
      data: YoutubeDetailResolver
    }
  },
];

@NgModule({
  declarations: [

  ],
  imports: [
    RouterModule.forChild(routes)
  ]
})
export class PromotionRoutingModule { }
